<template>
  <div class="modal fade" id="articleModal"
  ref="articleModal"
  tabindex="-1" aria-labelledby="articleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="articleModalLabel">{{ isNew ? '' : '' }}</h5>
          <button type="button"
          class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <!-- ?  -->
            <div class="col-sm-4">
                <div class="mb-1">
                  <div class="form-group">
                      <label for="imageUrl" class="fw-bold"></label>
                      <input
                      v-model="tempArticle.imageUrl" type="text"
                      class="form-control"
                      placeholder="">
                  </div>
                  <img class="img-fluid" :src="tempArticle.imageUrl">
                </div>
                <div class="form-group mb-3">
                    <label for="photoFile" class="fw-bold"></label>
                    <input type="file"
                    id="photoFile"
                    class="form-control" placeholder=""
                    @change="uploadImage">
                </div>
                <hr>
                <!-- ?  -->
                <div class="mb-1 fw-bold"></div>
                <div v-if="Array.isArray(tempArticle.imagesUrl)">
                    <div class="mb-1" v-for="(image, index) in tempArticle.imagesUrl" :key="index">
                        <label for="imageUrl"></label>
                        <input
                        v-model="tempArticle.imagesUrl[index]"
                        type="text"
                        class="form-control"
                        placeholder="">
                        <img :src="tempArticle.imagesUrl[index]" class="img-fluid">
                    </div>
                    <div
                    v-if="!tempArticle.imagesUrl.length ||
                    tempArticle.imagesUrl[tempArticle.imagesUrl.length - 1]">
                        <button
                        class="btn btn-outline-primary btn-sm d-block w-100"
                        @click="tempArticle.imagesUrl.push('')">
                            
                        </button>
                    </div>
                    <div v-else>
                        <button
                        class="btn btn-outline-danger btn-sm d-block w-100"
                        @click="tempArticle.imagesUrl.pop()">
                            
                        </button>
                    </div>
                </div>
                <div class="mt-2">
                    <button
                    class="btn btn-sm btn-danger d-block w-100"
                    @click="removeImages()"></button>
                </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                  <label for="title"></label>
                  <input v-model="tempArticle.title" id="title"
                  type="text"
                  class="form-control"
                  placeholder="">
              </div>
                <div class="form-group mb-3">
                  <label for="category"></label>
                  <input
                    v-model="tempArticle.author"
                    id="category"
                    type="text"
                    class="form-control"
                    placeholder=""
                  />
                </div>
                <!--  -->
                <div class="form-group">
                    <div class="mb-1" v-for="(item, index) in tempArticle.tag" :key="index">
                        <label for="tag">{{ index + 1 }}</label>
                        <input
                        v-model="tempArticle.tag[index]"
                        type="text"
                        class="form-control"
                        placeholder="">
                    </div>
                    <div class="btn-group w-100">
                      <button
                        class="btn btn-outline-primary btn-sm d-block w-100"
                        @click="tempArticle.tag.push('')">
                          
                      </button>
                      <button
                        class="btn btn-outline-danger btn-sm d-block w-100"
                        @click="tempArticle.tag.pop()">
                            
                      </button>

                    </div>
                </div>
                <hr>
                <div class="form-group mb-3">
                    <label for="content"></label>
                    <textarea
                    v-model="tempArticle.content"
                    id="content" type="text"
                    class="form-control"
                    placeholder=""
                    rows="1"
                    >
                    </textarea>
                </div>
                <div class="form-group mb-3">
                    <label for="description"></label><br>
                    <!-- <textarea v-model="tempArticle.description" id="description" type="text"
                    class="form-control" placeholder=""
                    rows="10"
                    ></textarea> -->
                    <p v-html="tempArticle.description"></p>
                    <ckeditor :editor="editor"
                    v-model="tempArticle.description"
                    :config="editorConfig"
                    ></ckeditor>
                </div>
                <div class="form-group">
                    <div class="form-check">
                        <input v-model="tempArticle.isPublic"
                        id="isPublic" class="form-check-input"
                        type="checkbox" :true-value="true" :false-value="false">
                        <label class="form-check-label" for="isPublic"></label>
                    </div>
                </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
            <button type="button"
            class="btn btn-primary"
            @click="$emit('update-article', tempArticle)"
            ></button>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

export default {
  props: ['article', 'isNew'],
  data() {
    return {
      articleModal: {},
      tempArticle: {},
      editor: ClassicEditor,
      editorData: '<p>Content of the editor.</p>',
      editorConfig: {
        // 
        placeholder: '...',
        // 
        initialData: '<h2></h2>',
        // 
        heading: {
        //  Heading 
          options: [
            {
              model: 'paragraph',
              title: 'Paragraph',
              class: 'ck-heading_paragraph',
            },
            {
              model: 'heading1',
              view: 'h2',
              title: 'Heading 1',
              class: 'ck-heading_heading1',
            },
            {
              model: 'heading2',
              view: 'h3',
              title: 'Heading 2',
              class: 'ck-heading_heading2',
            },
          ],
        },
        language: 'tw',
        // 
      },
    };
  },
  mounted() {
    this.articleModal = new Modal(this.$refs.articleModal);
  },
  watch: {
    article() {
      this.tempArticle = this.article;
    },
  },
  methods: {
    showModal() {
      this.articleModal.show();
    },
    hideModal() {
      this.articleModal.hide();
    },
    uploadImage() {
      // ! DOM
      const fileInput = document.querySelector('#photoFile');
      // fileInput
      const file = fileInput.files[0];
      // formData
      const formData = new FormData();
      // file-to-upload
      formData.append('file-to-upload', file);
      const url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/upload`;
      this.$http.post(url, formData).then((res) => {
        if (res.data.success) {
          // ! tempProduct.imageUrlimageUrl()imagesUrl
          if (!this.tempProduct.imageUrl) {
            this.tempArticle.imageUrl = res.data.imageUrl;
          } else {
            this.tempArticle.imagesUrl.push(res.data.imageUrl);
          }
          // value
          fileInput.value = '';
        }
      }).catch((err) => {
        console.log(err);
      });
    },
    removeImages() {
      if (this.tempArticle.imagesUrl.length) {
        this.tempArticle.imagesUrl.pop();
      } else {
        this.tempArticle.imageUrl = '';
      }
    },
  },
};
</script>
